.hljs {
    display: block;
    overflow-x: auto;
    padding: 0; 
    background-color: transparent;
    font-family: inherit;
}

.hljs code{
    font-family: inherit;
    font-weight: 600;
}

.hljs,
.hljs-subst {
    color: #444;
}

.hljs-comment {
    color: #888888;
}

.hljs-keyword,
.hljs-attribute,
.hljs-selector-tag,
.hljs-meta-keyword,
.hljs-doctag,
.hljs-name {
    font-weight: 600;
}

.hljs-type,
.hljs-string,
.hljs-number,
.hljs-selector-id,
.hljs-selector-class,
.hljs-quote,
.hljs-template-tag,
.hljs-deletion {
    /* color: #2D74EA; */
    color: #ff6900;
}

.hljs-title,
.hljs-section {
    /* color: #2D74EA; */
    color: #ff6900;
    font-weight: 600;
}

.hljs-regexp,
.hljs-symbol,
.hljs-variable,
.hljs-template-variable,
.hljs-link,
.hljs-selector-attr,
.hljs-selector-pseudo {
    /* color: #2D74EA; */
    color: #ff6900;
}

.hljs-literal {
    /* color: #2D74EA; */
    color: #ff6900;
}

.hljs-built_in,
.hljs-bullet,
.hljs-code,
.hljs-addition {
    /* color: #2D74EA; */
    color: #ff6900;
}

.hljs-meta {
    color: #9CB4B5;
}

.hljs-meta-string {
    color: #4d99bf;
}

.hljs-emphasis {
    font-style: italic;
}

.hljs-strong {
    font-weight: 600;
}


